<template>
  <n-data-table
      ref="table"
      :columns="columns"
      :data="data"
      :pagination="pagination"
      :row-key="dataKey"

  />
  <n-modal
      v-model:show="showModal"
      preset="dialog"
      title="房源信息"
      @positive-click="submitCallback"
      @negative-click="cancelCallback"
      negative-text="算了"
      positive-text="确认"
      size="small"
      :bordered="false"
      style="width: 1300px"

  >
    <template #header-extra> </template>
    <n-form



        ref="formRef"
        label-placement="left"
    >
      <n-grid :cols="25" :x-gap="25">
        <n-form-item-gi :span="5" label="姓名" path="inputValue">
          <n-input placeholder="姓名"  v-model:value="user.name"/>
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="头像" path="inputValue">
          <n-avatar :src="user.userface"/>
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="联系电话" path="inputValue">
          <n-input placeholder="联系电话" v-model:value="user.phone" />
        </n-form-item-gi>
      </n-grid>
      <n-grid :cols="25" :x-gap="25">
        <n-form-item-gi :span="5" label="小区" path="inputValue">
          <n-input placeholder="小区"  v-model:value="house.community"/>
        </n-form-item-gi>

        <n-form-item-gi :span="5" label="栋数" path="inputValue">
          <n-input placeholder="栋数" v-model:value="house.building" />
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="房间号" path="inputValue">
          <n-input placeholder="房间号"  v-model:value="house.door"/>
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="详细描述" path="inputValue">
          <n-input
              v-model:value="house.houseDescription"
              autosize
              type="textarea"
              placeholder="基本的 Textarea"
          />
        </n-form-item-gi>
      </n-grid>
      <n-grid :cols="25" :x-gap="25">
        <n-form-item-gi :span="5" label="面积" path="inputValue">
          <n-input placeholder="面积"  v-model:value="housebasic.area">
            <template #suffix>m<sup>2</sup></template>
          </n-input>

        </n-form-item-gi>

        <n-form-item-gi :span="5" label="朝向" path="inputValue">
          <n-select v-model:value="housebasic.direction" :options="direction" />
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="电梯" path="inputValue">
          <n-select v-model:value="housebasic.elevator" :options="elevator" />
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="楼层" path="inputValue">
          <n-input placeholder="面积"  v-model:value="housebasic.floor">
            <template #suffix>层</template>
          </n-input>
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="房间类型" path="inputValue">
          <n-select v-model:value="housebasic.houseType" :options="houseType" />
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="租期" path="inputValue">
          <n-select v-model:value="housebasic.lease" :options="lease" />
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="地铁线" path="inputValue">
          <n-select v-model:value="housebasic.metroLine" :options="metroLine" />
        </n-form-item-gi>

        <n-form-item-gi :span="5" label="车位" path="inputValue">
          <n-select v-model:value="housebasic.parking" :options="parking" />
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="区" path="inputValue">
          <n-select v-model:value="housebasic.region" :options="region" />
        </n-form-item-gi>
        <n-form-item-gi :span="5" label="租金" path="inputValue">
          <n-input placeholder="租金"  v-model:value="housebasic.rent">
            <template #suffix>元</template>
          </n-input>

        </n-form-item-gi>



      </n-grid>

      <n-checkbox-group v-model:value="houseInstallationList" >
        <n-space item-style="display: flex;">
          <n-checkbox value="1" label="洗衣机" />
          <n-checkbox value="2" label="空调" />
          <n-checkbox value="3" label="衣柜" />
          <n-checkbox value="4" label="电视" />
          <n-checkbox value="5" label="冰箱" />
          <n-checkbox value="6" label="热水器" />
          <n-checkbox value="7" label="床" />
          <n-checkbox value="8" label="暖气" />
          <n-checkbox value="9" label="宽带" />
          <n-checkbox value="10" label="天然气" />


        </n-space>
      </n-checkbox-group>

      <n-grid :cols="25" :x-gap="25">
        <n-form-item-gi :span="5" label="上架信息" path="inputValue">
          <n-switch v-model:value="house.publishState" @update:value="publish">
            <template #checked>该房源信息已经上架</template>
            <template #unchecked>该房源信息已经下架</template>
          </n-switch>

        </n-form-item-gi>
      </n-grid>




    </n-form>
    <template #footer> 尾部 </template>
  </n-modal>
</template>

<script>
import {h, defineComponent, ref} from 'vue'
import {NTag, NButton, useMessage, NAvatar} from 'naive-ui'
import {getRequest, putRequest} from "../../api/request";
import {onMounted, reactive} from "vue";

const  createColumns = ({ check }) => {
  return[
  {
    type: 'selection',
    fixed: 'left'
  },
    {
      title: 'id',
      key: 'id'

    },
  {
    title: 'Name',
    key: 'action',
    fixed: 'left',
    render(row){
      return h(NButton,{text:true},{default:()=>row.user.name})
    }

  },
    {
      title: 'UserFace',
      key: 'action',
      fixed: 'left',
      render(row){
        return h(NAvatar,{src:row.user.userface},{})
      }

    },

    {
      title: 'community',
      key: 'community'

    },

    {
      title: 'region',
      key: 'action',
      fixed: 'left',
      render(row){
        return h(NButton,{text:true},{default:()=>row.housebasic.region})
      }

    },
    {
      title: 'rent',
      key: 'action',
      fixed: 'left',
      render(row){
        return h(NButton,{text:true},{default:()=>row.housebasic.rent+"元"})
      }

    },
    {
      title: '状态',
      key: 'action',
      fixed: 'left',
      render(row){
        if (row.publishState===false){
          return h(NTag,{type:"warning",size:'small'},{default:()=>"未上架"})
        }else if (row.publishState===true){
          return h(NTag,{type:"success",size:'small'},{default:()=>"该房源已经上架!"})
        }

      }

    },
    {
      title: '编辑/审核',
      key: 'action',
      fixed: 'left',
      render(row){
        return h(NButton,{type:"success",size:'tiny',onClick:()=>check(row)},{default:()=>"编辑与审核"})
      }

    },


]}





export default {
  name: "index",
  setup(){
    const message=useMessage()
    const checkedRowKeys=ref([])
    const data=ref([])
    const showModal=ref(false)
     const user=ref({name:''})//用户部分信息
    const house=reactive({id:'',building:'',community:'',door:'',houseDescription:'',publishState:''})
    const housebasic=ref({})
    const lease=ref([])
    const houseType=ref([])
    const  direction=ref([])
    const elevator=ref([])
    const parking=ref([])
    const metroLine=ref([])
    const region=ref([])
    const mode=ref([])

    const houseInstallationList=ref([])
    const pagination=reactive({
      page:1,
      pageSize:3,
      showSizePicker: true,
      pageSizes: [3, 5, 7],
      onChange: (page) => {
        pagination.page = page
      },
      onPageSizeChange: (pageSize) => {
        pagination.pageSize = pageSize
        pagination.page = 1
      }
    })
    function getAll(){
      getRequest('/house/all/?page='+pagination.page+"&&size="+pagination.pageSize).then(resp=>{
        if (resp){
          data.value=resp.data.data
        }

      })
    }
    function allWithChildren() {
      getRequest('/search/allWithChildren').then(resp=>{
        lease.value=resp.data.lease;
        houseType.value=resp.data.houseType;
        direction.value=resp.data.direction;
        elevator.value=resp.data.elevator;
        parking.value=resp.data.parking;
        metroLine.value=resp.data.metroLine;
        region.value=resp.data.region;
        mode.value=resp.data.mode

      })
    }
    function creatArray(data){
      var hi=[]
      for (var d in data){
        hi.push((data[d].iid).toString())


      }


      houseInstallationList.value=hi;
      // console.log(data[0].houseId)

    }
    function publish() {
      putRequest('/house/updatePublish/'+house.id+"/"+user.value.id).then(resp=>{
        if (resp){}
        message.success("修改成功，无需点击更新确定按钮！")
        getAll()
        showModal.value=false
      })
    }

    onMounted(getAll)
    onMounted(allWithChildren)
    return{
      publish,

      creatArray,
      houseInstallationList,
      allWithChildren,
      lease,
      houseType,
      direction,
      elevator,
      parking,
      metroLine,
      region,
      mode,
      housebasic,
      house,
      user,
      showModal,
      checkedRowKeys,
      handleCheck (rowKeys) {
        checkedRowKeys.value = rowKeys
      },
      data,
      getAll,
      pagination,
      dataKey: (userData) => {
        return userData.key = userData.id;
      },

      columns: createColumns({
       check(row){


           getRequest('/house/findById/'+row.id).then(resp=>{
             if (resp){
               showModal.value=true;
               user.value=resp.data.user;
                   house.id=resp.data.id;
                   house.door=resp.data.door;
                   house.building=resp.data.building;
                   house.houseDescription=resp.data.houseDescription;
                   house.community=resp.data.community;
                   house.publishState=resp.data.publishState;
                   housebasic.value=resp.data.housebasic;
                   creatArray(resp.data.houseInstallationList)
             }
             console.log(resp.data)
           })

       console.log(row.id)
         message.loading("正在加载中...")

       }
      }),
      submitCallback:()=>{
       // console.log(houseInstallationList.value)
        putRequest("/house/updateHouse",housebasic.value).then(resp=>{
          if (resp.data){
            putRequest("/house/updateIns/?houseId="+house.id+"&&ids="+houseInstallationList.value).then(
                resp=>{
                  if(resp.data){
                    message.success('信息已经成功更新！')
                  }
                }
            )
          }
          console.log(resp.data)
        })

      },
      cancelCallback:()=>{
        message.info('取消！')
      }

    }
  }
}
</script>

<style scoped>

</style>
